<template>
  <div>
    <el-menu default-active="$route.path" class="el-head-menu" router>
      <el-menu-item class="item" index="/TeacherHead">
        <i class="el-icon-s-home"></i>
        <span class="item"></span>
      </el-menu-item>
      <el-submenu index="1">
        <template slot="title"><i class="el-icon-location"></i><span></span></template>
        <el-menu-item index="/TeacherCourse/AllCourse"></el-menu-item>
        <el-menu-item index="/TeacherCourse/ManageCourse"></el-menu-item>
      </el-submenu>
      <el-menu-item class="item2" index="/TeacherHomework">
        <i class="el-icon-s-claim"></i>
        <span class="item2"></span>
      </el-menu-item>
      <el-submenu index="3">
        <template slot="title"><i class="el-icon-share"></i><span class="item"></span></template>
        <el-menu-item class="subitem" index="/TeacherCommentAndDiscuss/TeacherAllComment"></el-menu-item>
        <el-menu-item class="subitem" index="/TeacherCommentAndDiscuss/TeacherAllDiscuss"></el-menu-item>
      </el-submenu>
      <el-submenu index="4">
        <template slot="title"><i class="el-icon-setting"></i><span></span></template>
        <el-menu-item index="/TeacherInformation/TeacherStatistics"></el-menu-item>
        <el-menu-item index="/TeacherInformation/TeacherChange"></el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>

<style>
@import "../../assets/css/menu.css";
</style>

<script>
export default {
  name: 'StudentMenu',
  data () {
    return {
      userName: '',
      userNickName: '',
      isCollapsed: this.$root.isCollapsed
    }
  },
  methods: {
    handleOpenAndClose: function () {
      this.isCollapsed = !this.isCollapsed
      this.$root.isCollapsed = this.isCollapsed
    }
  }
}
</script>



