<template>
  <div>
    <el-container class="background">
      <el-aside class="aside" width="show?'64px':'250px'">
        <TeacherMenu></TeacherMenu>
      </el-aside>
      <el-container class="main">
        <el-header class="header" style="background: white">
          <div class="heading_left">
            <i class="el-icon-edit">ICP</i>
          </div>
          <div class="heading_right">
            <el-avatar style="margin-top: 10px;margin-right: 10px"> {{ userNickName }}</el-avatar>
            <el-dropdown @command="handleCommand"><span class="el-dropdown-link">{{ userNickName }}<i
              class="el-icon-arrow-down el-icon--right"></i></span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="goToHelloWorld"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-header>
        <el-main style="padding-left: 10%; padding-right: 10%">
          <h2>ICP</h2>
          <h1></h1>
          <p style="line-height: 2;">ICP(Multi-functional Integrated Course Platform)</p>
          <p>ICP </p>
          <p></p>
          <p>ICP </p>
          <h1></h1>
          
          <ul>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
          </ul>
          <h4></h4>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import TeacherMenu from './TeacherMenu'
export default {
  name: 'TeacherHead',
  components: {TeacherMenu},
  data: function () {
    return {
      circleUrl: '',
      userName: '',
      userNickName: '',
      show: false
    }
  },
  mounted: function () {
    this.userName = this.cookie.getCookie('userName')
    this.userNickName = this.cookie.getCookie('userNickName')
  },
  methods: {
    goToHelloWorld: function () {
      this.cookie.clearCookie('userName')
      this.cookie.clearCookie('userNickName')
      this.$router.replace('/')
    },
    handleCommand: function (command) {
      this.goToHelloWorld()
    }
  }
}
</script>

<style scoped>
  @import "../../assets/css/back.css";
</style>



